<template>
  <div v-zinx="{ page: 'limit' }" class="page stop_notice">
    <div class="stop_notice_fixed">
      <div class="stop_notice_box">
        <div class="kefu_btn c_box">
          <div v-zoom class="cursor_point c_box" @click="onJump">
            <icon-font name="icon-Icon_Service2" :size="commonStyle('stop_notice_kefu_icon_size')" />
          </div>
        </div>
        <div class="stop_nodtice_img">
          <img :src="$loadIcon('logo_lg')" alt="" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { lodash, getEeplace } from '@/utils/helper.js'
import _common from '@style/views/limitation.modules.scss'

export default {
  name: 'stopNotice',
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['systemConfig', 'isApp', 'stopNotice'])
  },
  mounted() {
    this.onStopgsNotice()
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    // 停服公告
    async onStopgsNotice() {
      const { content = '', title = '' } = this.stopNotice
      this.$pop
        .confirm({
          title: title || '',
          content: getEeplace(content || ''),
          confirmText: this.$t('General.General_1')
        })
        .show()
    },
    // 跳转客服
    onJump() {
      const { kf_show = {} } = this.systemConfig
      const { kf_chat_url = '' } = kf_show && lodash.isObject(kf_show) ? kf_show : {}

      window.location.href = kf_chat_url ? kf_chat_url : VUE_APP_KF_LINK
    }
  },
  beforeDestroy() {
    this.$pop.confirm().hide()
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/limitation.modules.scss';

// 针对移动端样式
.stop_notice_fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: $stop_notice_bg;
  .stop_notice_box {
    width: 100%;
    height: 100%;
    position: relative;
    .kefu_btn {
      width: $stop_notice_kefu_btn_width;
      height: $stop_notice_kefu_btn_height;
      position: absolute;
      right: $stop_notice_kefu_btn_right;
      top: $stop_notice_kefu_btn_top;
      background: $primary_dark_color;
      border-radius: $stop_notice_kefu_btn_radius;
    }
    .stop_nodtice_img {
      width: $stop_notice_img_app_width;
      position: absolute;
      top: $stop_notice_img_top;
      left: $stop_notice_img_left;
      transform: translate(-50%, -50%);
      img {
        width: 100%;
      }
    }
  }
}

// 针对pc样式
@include breakpoint-only-up() {
  .stop_notice_fixed {
    .stop_notice_box {
      .stop_nodtice_img {
        width: $stop_notice_img_pc_width;
      }
    }
  }
}
</style>
